    .subtitle::after{
        border-bottom: 2px solid grey;
        display:block;
        margin: 0px 0px;
        border-bottom: $border-standard-width $border-standard-style $border-standard-color;
        content:'';
        width: 100%;
        box-sizing: border-box; 
    }

    .container-column{
        color: white;
        font-family: monospace;
        display:flex;
