<template>
    <div style='height: 100%; width: 100%; display: grid; grid-template-rows: 1fr auto; grid-row-gap: 8px; box-sizing: border-box;'>
        <div style='position:relative; width: 100%; height: 100%; width: 100%;'>
            <div style='position: absolute; width: 100%; bottom:10%; color: white; text-align:center; font-size: 1.2rem; font-weight: 600;' v-html='this.displayValue'></div>
            <canvas :id='chartID' class='gauge' style='height: 100%; width: 100%;' height='auto' width='auto'></canvas>
        </div>
        <div style='color: white; text-align: center; font-size: 1.5rem; font-weight: 600;' v-html='chartTitle'/>
    </div>
</template>
<script>
import Chart from 'chart.js';
export default {
    props:['chartTitle','displayValue', 'chartID','chartLabels','chartValues','chartType','chartColors','displayLegend'],

    data(){
        return{
            chart:'',
        }
    },

    computed:{
        updateChart:function(){
            let chartData = this.chartValues
            return chartData 
        },

        scalePresent:function(){
            if(this.chartType === 'bar'){
                return { yAxes: [{ ticks: { beginAtZero: true } }] }  
            }
        }
    },

    watch:{
        updateChart: function(){
            this.chart.data.datasets[0].data.pop()
            this.chart.data.datasets[0].data = this.updateChart
            this.chart.update()
        }
    },

    mounted: function(){
    const ctx = document.getElementById(this.chartID);
    this.chart = new Chart(ctx,{
      type: this.chartType,
      data:{
        labels: this.chartLabels,
        datasets:[{
          backgroundColor:this.chartColors,
          data:this.chartValues,
        }]
      },
      rotation: Math.PI * -.5,
      options:{
            legend:{
                display: this.displayLegend
            },
            animation:{
                duration: .1
            },
            defaultFontFamily:'Monospace',
            defaultFontStyle: 'normal',
            defaultFontColor:'#fff', 
            cutoutPercentage: 70,
            responsive: true,
            maintainAspectRatio: false,
            drawborder: false,            
            scales: this.scalePresent,        
            rotation: Math.PI,
            circumference: 1 * Math.PI,
        },
        centerText:{
            display:true,
            text:'TESTING',
        },
    })  
  }
}
</script>

<style>    
    canvas{
        position: absolute;
        height: auto;
        width: 100%;
        margin: auto auto;
    }
</style>
