export default{
    state:{
        baselineModel:{model:'baselineModel'},
        location:{location:'mars'},
        permanentStay: {state:false},
        duration:{amount:0},
        durationUnits:{type:'days'},
        humans:{agent:'human_agent',amount:0},
        foodMass:{agent:'food_storage',amount:0},
        crewQuarters:{
            'small':{'agent':'crew_habitat_small','amount':0},
            'medium':{'agent':'crew_habitat_medium','amount':0},
            'large':{'agent':'crew_habitat_large','amount':0},
            'xlarge':{'agent':'crew_habitat_xlarge','amount':0}
        },
        selectedCrewQuarters:'none',
        greenhouse:{
            'small':{'agent':'greenhouse_small','amount':0},
            'medium':{'agent':'greenhouse_medium','amount':0},
            'large':{'agent':'greenhouse_large','amount':0}
        },
        selectedGreenhouse:'none',
        lifesupport:{amount:0},
        seed:[], //Push in seed type as {agent:'seed name',amount:#}
        powerGeneration:'',
        powerStorage:{amount:0},
        powerRacks:{amount:0},        
    },

    getters:{
        currentConfiguration: state =>{
            var agents = {"agents":{
                "human_agent":[{"connections":{},"amount":state.human.amount}],
                "green_house_small":[{"connections":{},"amount":state.greenhouse.small.amount}],
                "green_house_medium":[{"connections":{},"amount":state.greenhouse.medium.amount}],
                "green_house_large":[{"connections":{},"amount":state.greenhouse.large.amount}],
                
            }}
        },

        eclssRack: state=>{

        }
    },

    mutations:{
        SETBASELINEMODEL(state,value){
            state.baselineModel.model = value;
        },

        SETLOCATION(state,value){
            state.location.location = value;
        },

        SETPERNANENTSTAY(state,value){
            state.permanentStay.state = value;
        },

        SETDURATION(state,value){
            state.duration.amount=value;
        },

        SETDURATIONUNITS(state,value){
            state.durationUnits.type = value;
        },

        SETHUMANS(state,value){
             state.humans.amount = value;

        },

        SETFOODMASS(state,value){
            state.foodMass.amount = value;
            console.log(state.foodMass.amount);
        },

        SETCREWQUARTERS(state,value){
            state.selectedCrewQuarters = value;
            state.crewQuarters[value].amount += 1;
            state.selectedCrewQuarters = state.crewQuarters[value].agent;
        },

        SETGREENHOUSE(state,value){
            state.selectedGreenhouse = value;
            state.greenhouse[value].amount += 1;
            state.selectedGreenhouse = state.greenhouse[value].agent;
        },

        SETLIFESUPPORT(state,value){
            state.lifesupport.amount = value;
        },

        SETSEED(state,value){
            state.seed = value;
        },

        SETPOWERGENERATION(state,value){
            state.powerGeneration = value;
        },

        SETPOWERSTORAGE(state,value){
            state.powerStorage.amount = value;
        },

        SETPOWERRACKS(state,value){
            state.powerRacks.amount = value;
        }
    },

    actions:{},

}