<template>
    <div class='content'>
        <chart class='gauge' :chartTitle="'CO<sup>2</sup> %'" :displayValue="'.035%'" :chartID ="'1'" :chartLabels="['Carbon Dioxide']" :chartColors="['#00ab98']" :chartValues="[.0035,.0065]" :chartType="'doughnut'"/>
        <chart class='gauge' :chartTitle="'O<sup>2</sup> %'" :displayValue="'21.5%'" :chartID ="'2'" :chartLabels="['Oxygen']" :chartColors="['#00ab98']" :chartValues="[21,2.5]" :chartType="'doughnut'"/>
        <chart class='gauge' :chartTitle="'CH<sup>4</sup>'" :displayValue="'4.2x10<sup>-5</sup>%'" :chartID ="'3'" :chartLabels="['Methane']" :chartColors="['#00ab98']" :chartValues="[.000017,.000003]" :chartType="'doughnut'"/>
        <chart class='gauge' :chartTitle="'H<sup>2</sup>O Atmos.'" :displayValue="'.7%'" :chartID ="'4'" :chartLabels="['Humidity']" :chartColors="['#00ab98']" :chartValues="[.7,99.3]" :chartType="'doughnut'"/>
        <chart class='gauge' :chartTitle="'H<sup>2</sup>O Potable'" :displayValue="'987 kg'" :chartID ="'5'" :chartLabels="['Potable']" :chartColors="['#00ab98']" :chartValues="[987,13]" :chartType="'doughnut'"/>
        <chart class='gauge' :chartTitle="'H<sup>2</sup>O Waste'" :displayValue="'101.02'" :chartID ="'6'" :chartLabels="['Waste']" :chartColors="['#00ab98']" :chartValues="[101.02,498.98]" :chartType="'doughnut'"/>
    </div>
</template>

<script>
import Chart from '@/components/Chart'
import {mapState,mapMutations} from 'vuex'
export default {
    components:{
        'chart':Chart,
    },
    computed:{
        ...mapState({            
            population: state => state.sessionStep.human_agent.amount,
            currentTime: state => state.sessionStep.currentTime,
            co2Ratio: state => state.sessionStep.atmo_co2_ratio,
            o2Ratio: state => state.sessionStep.atmo_o2_ratio,
            kwhEnrgProduced: state => state.sessionStep.kwh_enrg_produced,
            kwhEnrgConsumed: state => state.sessionStep.kwh_enrg_consumed,
        }),

        co2Remainder: function(){          
            return Math.max(0,(.013 - this.co2Ratio.amount).toFixed(4));
        },

        o2Remainder: function(){
            return Math.max(0,(.25 - this.co2Ratio.amount).toFixed(4));
        },


    },
}
</script>

<style lang="scss" scoped>
    .content{
        height: 100%;
        display: grid;
        grid-template-rows: auto auto auto;
        grid-template-columns: repeat(2,1fr);
        grid-row-gap: 16px;
        box-sizing: border-box;
    }


</style>
