export default{
    state:{
        human_agent:{'amount':0},
        atmo_co2_ratio:{'amount':0},
        atmo_o2_ratio:{'amount':0},
        atmo_co2_produced:{'amount':0},
        atmo_o2_produced:{'amount':0},
        atmo_co2_consumed:{'amount':0},
        atmo_o2_consumed:{'amount':0},
        kwh_enrg_produced:{'amount':0},
        kwh_enrg_consumed:{'amount':0},
        food_edible_consumed:{'amount':0},
        h2o_potb_consumed:{'amount':0},
        h2o_potb_produced:{'amount':0},

        currentTime:0,
    },

    getters:{

    },

    mutations:{
        MUTATESTEPDATA(state,value){
            state.human_agent.amount = value.human_agent;            
            state.atmo_co2_ratio.amount = value.atmo_co2_ratio;
            state.atmo_o2_ratio.amount = value.atmo_o2_ratio;
            state.kwh_enrg_produced.amount = value.enrg_kwh_produced;
            state.kwh_enrg_consumed.amount = value.enrg_kwh_consumed;
            state.currentTime = value.step_number;
            state.atmo_co2_consumed.amount = value.atmo_co2_consumed;
            state.atmo_co2_produced.amount = value.atmo_co2_produced;
            state.atmo_o2_consumed.amount = value.atmo_o2_consumed;
            state.atmo_o2_produced.amount = value.atmo_o2_produced;
            state.food_edible_consumed.amount = value.food_edible_consumed;
            state.h2o_potb_consumed.amount = value.h2o_potb_consumed;
            state.h2o_potb_produced.amount = value.h2o_potb_produced;
        }
    },

    actions:{
        updateStepData(context, value){
            context.commit('MUTATESTEPDATA',value);
        }
    },
}