                    this.SETDURATIONUNITS(this.timeType);
                    this.SETHUMANS(this.human_agent);
                    this.SETFOODMASS(this.foodMass);  
                    this.SETCREWQUARTERS(this.crewQuarters);
                    this.SETLIFESUPPORT(this.eclss);
                    this.SETGREENHOUSE(this.greenhouse);
                    this.SETSEED(this.selectedSeed);   
                    this.SETPOWERGENERATION(this.powerSource);
                    this.SETPOWERSTORAGE(this.storage);
                    //this.SETPOWERRACKS(this.solarRacks);               
                }
            }).catch(error=>{
