<template>
    <div class='session__panel'>
        <div class='panel__title'>
            <div class='content'>
                {{title}}
                <b-dropdown class='panel-dropdown panel__dropdown' id="panel-dropdown"  variant="link" right no-caret >
                    <template slot="button-content">
                        <fa-icon class='fa-icon' transform='grow-8' :icon="['fal','ellipsis-h-alt']"/>
                    </template>
                        <b-dropdown-item class='panel__dropdown--item' href="#" v-on:click="setPanelTitle('CONFIGURATION');setActivePanel('configuration')" >CONFIGURATION</b-dropdown-item>
                        <!--<b-dropdown-item class='panel__dropdown--item' href="#" v-on:click="setPanelTitle('PAYLOAD - NEXT ARRIVAL');setActivePanel('payload')">Payload</b-dropdown-item>-->
                        <b-dropdown-item class='panel__dropdown--item' href="#" v-on:click="setPanelTitle('ECOSYSTEM');setActivePanel('ecosystem')">ECOSYSTEM</b-dropdown-item>
                        <b-dropdown-item class='panel__dropdown--item' href="#" v-on:click="setPanelTitle('LIVE AGENT FEED');setActivePanel('feed')">LIVE FEED</b-dropdown-item>
                        <b-dropdown-item class='panel__dropdown--item' href="#" v-on:click="setPanelTitle('MISSION STATS');setActivePanel('mission')">MISSION STATS</b-dropdown-item>
                </b-dropdown>
            </div>
        </div>
        <div class='panel__content'>
            <keep-alive>
                <component :is='panelContent' class='content'/>
            </keep-alive>
        </div>

    </div>
</template>

<script>
import Configuration from '@/components/PanelConfiguration'
import Payload from '@/components/PanelPayload'
import Mission from '@/components/PanelMission'
import Gauges from '@/components/PanelEcosystem'
import Feed from '@/components/PanelFeed'
import {mapState,mapMutations} from 'vuex'
export default {
    props:['panelType','panelTitle'],
    components:{
        'configuration':Configuration,
        'payload':Payload,
        'mission':Mission,
        'ecosystem':Gauges,
        'feed':Feed,
    },

    data(){
        return{
            title:this.panelTitle,
            panelContent:this.panelType,
        }
    },

    methods:{
        setPanelTitle:function(value){
            this.title = value;
        },

        setActivePanel:function(value){
            this.panelContent = value;
        }
    }   
}
</script>

<style lang='scss' scoped>
@import '../scss/_theme';
    .session__panel{
        @extend %display-panel;
        display: flex;
        flex-direction:column;
    }

    .panel__title{
        @extend %font-panel-title;
        margin: auto 0px auto 0px;      
    
        .content{
            display: flex;
            justify-content: space-between;
            align-items: center;  
        }

        .panel__dropdown{
            background-color: transparent;
            
        }

        .panel__dropdown--item{
            @extend %font-text;
            color: black;
        }
    }

    .panel__dropdown--item:hover,.panel__dropdown--item:focus{
        background-color: #acacac;
    }

    .panel__title::after{
        display:block;
        margin: 0px 0px;
        margin-top:auto;
        margin-bottom: 8px;
        border-bottom: $border-standard-width $border-standard-style $border-standard-color;
        content:'';
    }

    .fa-icon{
        margin: auto 16px;
        color: white;
    }

    .panel__content{
        width: 100%;
        height: 100%;
    }

</style>
