import store from '../store'
import sessionData from '@/store/modules/sessionStep'
import axios from 'axios'

import StepUpdate from '@/javascript/stepData'
axios.defaults.withCredentials = true;

var step_num = 0;
var stepSize = 1;

var updateInterval = 2000;
var updateIntervalID = null;
var updateRunning = true;

var currentStep = null;
require('promise.prototype.finally');

function updateStep(){
    var path = '/get_step';
    if(currentStep !== null){
        currentStep += stepSize;
        path += '?step_num=' + currentStep;
    }
    var isTerminated = false;
    axios.get(path).then(response =>{
        if(response.status == 200){
            var totalAgentTypes = response.data.agents.total_agent_types;
            var totalConsumption = response.data.agents.total_consumption;
            var totalProduction = response.data.agents.total_production;
            //var storageAgentTypes = response.data.storages.
            var modelStats = response.data.model_stats;
            console.log(response.data);
            if(!isTerminated){
                isTerminated = response.data.is_terminated; 
                store.dispatch('updateStepData',{
                    'human_agent':totalAgentTypes.human_agent,
                    'atmo_co2_ratio':modelStats.air_storage_1.atmo_co2_ratio,
                    'atmo_o2_ratio':modelStats.air_storage_1.atmo_o2_ratio,
                    'enrg_kwh_produced':totalProduction.enrg_kwh,
                    'enrg_kwh_consumed':totalConsumption.enrg_kwh,
                    'atmo_co2_consumed':totalConsumption.atmo_co2,
                    'atmo_co2_produced':totalProduction.atmo_co2,
                    'atmo_o2_consumed':totalConsumption.atmo_o2,
                    'atmo_o2_produced':totalProduction.atmo_o2,
                    'food_edible_consumed':totalConsumption.food_edible,
                    'h2o_potb_consumed':totalConsumption.h2o_potb,
                    'h2o_potb_produced':totalProduction.h2o_potb,
                    'step_number':response.data.step,   
                    //Food
                    //Water  
                });
            }           
        }
    }).catch(error=>{
        console.log(error.response);
    }).finally(function(){
        StepUpdate.setUpdateInterval();
    })
}    

export default{    
    setUpdateInterval: function(){
        updateIntervalID = setTimeout(function(){
            if(updateRunning){
                updateStep();
            }
        },updateInterval);
    },   
}