<template>
    <div class='toolbar-container'>
        <div class='toolbar__time'>SOL: {{clock}}</div>
        <div class='toolbar__content'>
            <div class='toolbar__title'>SESSION DASHBOARD</div>
            <div class='menu__item menu__item--selected'>DASHBOARD</div>
            <div class='menu__item' style='color:grey'>PAYLOAD CREATOR</div>
            <div class='menu__item' style='color:grey'>BUILD MODE</div>
            <div class='menu__item'>SIMOC ENCYLOPEDIA</div>
            <div class='menu__item'>SESSION CONFIGURATION</div>
            <div class='toolbar__title--menu' style='color:grey'>MAIN MENU</div>
        </div>

        <div class='toolbar__time__control'>
            <div class='timer__control' style='font-size: 2rem;'>
                <fa-icon class='fa-icon' :icon="['fal','caret-circle-left']" style='margin:auto auto; color:white;'/>
                <fa-icon class='fa-icon' :icon="['fal','play-circle']" style='margin:auto auto; color:white;'/>
                <fa-icon class='fa-icon' :icon="['fal','caret-circle-right']" style='margin:auto auto; color:white;'/>
                
            </div>
        </div>
    </div>
</template>

<script>
import {mapState,mapMutations} from 'vuex'
export default {
    computed:{
        ...mapState({
            currentTime: state => state.sessionStep.currentTime,
        
            clock: function(){
                var sols = this.currentTime / 24.667;
                sols = sols.toFixed(0);
                return sols;
            }
        })
    },
}
</script>

